.mg-btm-small {
  margin-bottom: 1rem;
}
.mg-btm-medium {
  margin-bottom: 2rem;
}
.text-bold {
  font-weight: 700;
}

.heading-secondary {
  font-size: 1.6rem;
  font-weight: 700;
  //   line-height: 3;
}

.heading-primary {
  font-size: 2rem;
  font-weight: 700;
}

.fa {
  padding: 1rem;
  font-size: 1rem;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;

  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

.fa:hover {
  opacity: 0.7;
}

.fa-facebook {
  background: #3b5998;
  color: white;
}

.fa-google {
  background: #3b5998;
  color: white;
}

.fa-google {
  background: #dd4b39;
  color: white;
}

.btn {
  width: 100%;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  //   background: none;
  &__primary {
    padding: 1rem;
    background-color: var(--btn-primary-color);
    font-size: 1rem;
    font-weight: 700;
    color: var(--color-white);
    &:hover {
      background-color: var(--btn-primary-color-dark);
    }
  }
}
