:root {
  --bg-dark-primary: #111;
  --color-white: #fff;
  --color-grey-dark: rgba(63, 62, 62, 0.6);

  --color-grey-dark-1: rgba(134, 132, 132, 0.6);
  --color-grey-dark-2: rgba(90, 90, 90, 1);
  --bg-light: rgba(0, 0, 0, 0.5);
  --bp-small: 32.5em;
  --bp-medium: 52.6em;
  --color-secondary: rgba(255, 127, 77, 0.945);
  --btn-primary-color: #e50914;
  --btn-primary-color-dark: #ad060f;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  font-size: 62.5%;
  @media screen and (max-width: 52.6em) {
    font-size: 50%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
