.Form {
  //   box-sizing: border-box;
  height: fit-content;
  margin: 0 auto;
  padding: 3rem 5rem;

  width: fit-content;
  background-color: var(--bg-dark-primary);

  &__group {
    display: block;
    height: 4rem;
    &:not(:last-child) {
      margin-bottom: 1rem;
    }
  }

  &__input {
    color: var(--color-white);
    width: 100%;
    height: 3rem;
    padding: 1rem;
    border: none;
    outline: none;
    font-size: 1rem;
    border-radius: 3px;
    background-color: var(--color-grey-dark);

    &:focus {
      outline: none;
    }

    &:focus:invalid {
      border-bottom: 2px solid var(--color-secondary);
    }

    &::-webkit-input-placeholder {
      color: var(--color-grey-dark-1);
    }
  }

  &__label {
    display: block;
    font-size: 0.6rem;
    margin-left: 1rem;
    color: var(--color-grey-dark-1);
    transition: all 0.4s;
    transform: translateY(-2.8rem);
    opacity: 1;
    font-weight: 500;
  }

  &__input:placeholder-shown ~ &__label {
    transform: translateY(-2rem);
    opacity: 0;
  }

  &__checkbox {
    background-color: var(--color-grey-dark);
  }

  &__validationInfo {
    display: block;
    font-size: 0.8rem;
    font-weight: 400;
    visibility: visible;
    width: 100%;
    color: var(--color-secondary);
    transform: translateY(-0.5rem);
  }

  &__help {
    // background-color: ivory;
    margin-top: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.7rem;
    padding: 0.4rem 0;
    border-bottom: 1px solid var(--color-grey-dark);
  }
  &__helpLink {
    &:link,
    &:visited {
      text-decoration: none;
      color: inherit;
    }
  }

  &__helperText {
    padding: 1rem 0;
    text-align: center;
    font-size: 0.8rem;
  }
  &__socialLinks {
    padding: 0.4rem 0;
  }
}

.socialLink {
  &__list {
    list-style: none;

    display: flex;
    justify-content: space-evenly;
  }

  &__item {
  }
}
